import React from "react";
import { Carousel } from 'react-responsive-carousel';
import 'tachyons';
import './CarouselComponent.css'

function CarouselComponent() {
    return (
        <div className='slider ma4'>
            <Carousel 
            infiniteLoop 
            useKeyboardArrows 
            autoPlay
            showThumbs={false}
            showStatus={false}
            showArrows={false}>
                
                <div className='banner col s12 m7 card'>
                    <img src="../image-1.jpg"/>
                </div>
                <div  className='banner col s12 m7 card'>
                    <img src="../image-2.jpg"/>
                </div>
                <div  className='banner col s12 m7 card'>
                    <img src="../image-3.jpg"/>
                </div>
                
            </Carousel>
        </div>
    );
}
export default CarouselComponent
